<!--登录注册页面-->
<template>
	<div class="signin">
		<subpage-header></subpage-header>
		<div class="signinnav">
			<p :class="{active:true}">一号家帐号</p>
			<p>手机快捷登录</p>
		</div>
		<div class="adAndPws">
			<div class="ad">
				<input type="text" placeholder="请输入用户名" ref="name">
				<p>帐号</p>
			</div>
			<div class="pws">
				<input type="password" placeholder="请输入密码" ref="psw1">
				<p>密码</p>
			</div>
			<div class="pws" v-if="$store.state.sign">
				<input type="password" placeholder="请再次输入密码" ref="psw2">
				<p>确认密码</p>
			</div>
		</div>
		<div class="remember">
			<span class="rememberbtn"></span>
			<span>30天内免登录</span>
		</div>
		<div class="signinbtn" @click="next">{{ registerOrsign[$store.state.sign].btn }}</div>
		<div class="function">
			<span>找回密码</span>
			<span class="register" @click="changeState">{{ registerOrsign[$store.state.sign].change }} &gt</span>
		</div>
		<div class="fastLogin">
			<div class="tit">
				<h1>使用社交帐号登录</h1>
			</div>
			<div class="otherlogo">
				<div class="weibo"></div>
				<div class="weixin"></div>
				<div class="qq"></div>
			</div>
		</div>
	</div>
</template>
<script>
import subpageHeader from './subpageHeader'
	export default {
		name:'signIn',
//		数据
		data(){
			return {
				registerOrsign:[{btn:'登 录',change:'注册帐号'},{btn:'注 册',change:'去登录'}],
			}
		},
//		需要引入的模板
		components:{
			subpageHeader,
		},
		//函数
		methods: {
			changeState:function(){
				this.$store.commit('changesign');
			},
//			点击了登录or注册按钮
			next:function(){
				var str = this.$refs.name.value.trim();
				var str1 = this.$refs.psw1.value.trim();
				if(this.$store.state.sign){
//					如果是注册状态
					if(str&&str1){
						if(this.$refs.psw1.value == this.$refs.psw2.value){
							alert("注册成功,可登录");
							this.$store.commit('changesign');
							localStorage.setItem(str,str1);
						}else{
							alert("密码不一致，请重新输入");
						}
					}else{
						alert("帐号或密码不能为空");
					}
					
				}else{
//					想要登录
					if(str){
						if(localStorage.getItem(str)){
							if(localStorage.getItem(str) == str1){
								alert("登录成功，去逛逛");
								this.$store.commit('changelogged');
								this.$router.push('/furnishingMall');
							}
						}else{
						alert("帐号不存在");
						}
					}else{
						alert("请输入帐号");
					}
				}
			}
		}
	}
</script>
<style scoped>
	.signin {
		background: #f2f2f2;
		min-height: 78vh;
	}
	.signinnav {
		display: flex;
	}
	.signinnav p {
		flex-grow: 1;
		text-align: center;
		padding: 2vw;
		font-size: 5vw;
		border: 1px solid #dcdcdc;
		background: white;
	}
	.signinnav .active {
		color: #f15353;
	}
	
	/*帐号密码输入框*/
	.adAndPws {
		margin-top: 2vw;
		background: white;
	}
	.adAndPws div {
		height: 15vw;
		position: relative;
		color: rgba(0, 0, 0, 0.82);
	}
	.adAndPws div p {
		position: absolute;
		font-size: 5vw;
		top: 4.5vw;
		left: 3vw;
	}
	.adAndPws div input {
		border: none;
		position: absolute;
		width: 70%;
		height: 5vw%;
		padding: 5vw 5vw 5vw 25vw;
		border-bottom: 1px solid #ededed;
		font-size: 5vw;
	}
	/*记住密码按钮*/
	.remember {
		margin: 3vh 0;
		padding-left: 4vw;
		height: 4vh;
	}
	.rememberbtn {
		display: inline-block;
		width: 5vh;
		height: 5vh;
		background: url(../../static/img/checked-icon.png) no-repeat;
		background-size: 100%;
		background-position-y: 100%;
		margin-right: 4vw;
	} 
	.remember span {
		vertical-align: middle;
		font-size: 2.9vh;
	}
	.signinbtn {
		background: #fa4e3e;
		width: 94vw;
		height: 7.5vh;
		line-height: 7.5vh;
		font-size: 3.7vh;
		margin: 0 auto;
		text-align: center;
		color: white;
		border-radius: 2vw;
	}
	/*注册及找回密码*/
	.function {
		padding: 4vw;
	}
	.function span {
		font-size: 4.5vw;
	}
	.function .register {
		color: #fa4e3e;
		font-weight: bold;
		float: right;
	}
	.fastLogin {
		margin-top: 2vh;
	}
	.fastLogin .tit h1{
		text-align: center;
		font-size: 4.3vw;
		color: rgba(102, 102, 102, 0.91);
	}
	.otherlogo {
		width: 60vw;
		display: flex;
		margin: 5vw auto 0;
		justify-content: space-around;
	}
	.otherlogo div{
		width: 19vw;
		height: 19vw;
		background: url(../../static/img/fast-ico.png) no-repeat;
		background-size: 100%;
	}
	.otherlogo .weixin {
		background-position-y: 108%;
	}
	.otherlogo .qq {
		background-position-y: 53%;
	}
</style>


