<template>
  <div id="app">
    <router-view></router-view>
    <webappfooter v-if="!($route.path == '/shopingcart')"></webappfooter>
    <div class="speciafuntion" v-if="$store.state.shopingnone">
    		<functionbtn :text="'客服'" :backimg="'../static/img/a.png'"></functionbtn>
    		<functionbtn :text="'顶部'" :backimg="'../static/img/a.png'" @clickfunction="gotop"></functionbtn>
    </div>
  </div>
</template>

<script>
import webappfooter from './components/webappfooter'
import functionbtn from './components/functionbtn'
import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

const store = new Vuex.Store({
//	state 定义的是数据模型  不要直接修改模型的值
		state:{
			text:"全局vuex，app.vue",
			caddress:"北京",
			paddress:"中国",
			area:"天安门",
			maskshow:false,
			// 是否将回到顶部按钮隐藏shopingnone
			shopingnone:true,
			// 购物车商品数量
			shopnum:0,
//			是否进入登录页面,设置成1则是注册页面
			sign:0,
//			判断用户是否已登录
			logged:false,
		},
//		getters 得到是从state里处理过的数据,感觉类似于计算属性
		getters:{
			
		},
//		mutations 对数据操作的方法，定义方法的第一个参数指代 state 数据数据的操作只能通过触发mutations定义的方法this.$store.commit("add"),只能处理同步的方法
		mutations:{
			changepaddress:function(state,paddress){
				state.paddress = paddress;
			},
			changecaddress:function(state,caddress){
				state.caddress = caddress;
			},
			changearea:function(state,area){
				state.area = area;
			},
			changemaskshow:function(state){
				state.maskshow = !state.maskshow;
			},
			// 改变回到顶部按钮的状态
			changeshopingnone:function(state,boo){
				state.shopingnone = boo;
			},
			addshopnum:function(state,value){
				state.shopnum+=value;
			},
//			改变成登录或者注册页面
			changesign:function(state){
				state.sign = state.sign?0:1;
			},
			changelogged:function(state){
				state.logged = true;
			}
		},
//		actions 对数据操作的方法，可以处理异步，定义方法的第一个参数指代store
		actions: {
			
		}
})
export default {
  name: 'app',
  store,
  components: {
	webappfooter,
  	functionbtn,
  },
  methods:{
  	gotop(){
  		window.scrollTo(0,0)
  	}
  }
}
</script>

<style>
#app {
  font-family: '微软雅黑', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.speciafuntion {
	position: fixed;
	right: 6vw;
	bottom: 13vh;
}
</style>



