<template>
	<div>
		<ul class="decorationchildul">
			<a href="javascript:void(0)">
				<li v-for="item in pictitdetaileddata" class="clearfix">
					<img :src="item.img" alt="" />
					<div>
						<span>{{ item.tit }}</span>
						<p>{{ item.detailed }}</p>
					</div>
				</li>
			</a>
		</ul>
	</div>
</template>
<script>
	export default {
		name:'decorationchildul',
		props:["pictitdetaileddata"],
		data(){
			return {}
		},
		
	}
</script>
<style scoped>
	.decorationchildul {
		border-top: 1px dotted rgb(239, 239, 239);
	}
	.decorationchildul li {
		padding: 10px;
		color: #444444;
		border-bottom: 1px dotted rgb(239, 239, 239);
	}
	.decorationchildul li img {
		float: left;
		display: block;
		width: 35%;
		height: auto;
		min-height: 60px;

	}
	.decorationchildul li div {
		overflow: hidden;
		padding-left: 6px;
	}
	.decorationchildul li div span{
		font-size: 14px;
		display: block;
		white-space: nowrap;
		overflow: hidden;
		text-overflow:ellipsis;
	}
	.decorationchildul li div p {
		color: #aaaaaa;
		display: -webkit-box;
		-webkit-box-orient: vertical;
		-webkit-line-clamp: 3;
		overflow: hidden;
	}
</style>



