<template>
	<div class="indexAppreciate">
		<indexsmolltitle :tit="'参观功地'"></indexsmolltitle>
		<mt-swipe :show-indicators="false" class="imglistborder clearfix">
			<mt-swipe-item v-for="item in imglist">
				<img :src="item.CoverImageUrl" alt="" />
				<span>{{ item.Huxing }}</span>
				<span>{{ item.TotalPrices }}<i>￥</i></span>
				<div class="moreimg"></div>
			</mt-swipe-item>
		</mt-swipe>
		<div class="allchangebtn">
			<changeaset :font="'换一批'"
					:backimg="'/static/img/otherions.png'"
					:backimgposition="'-0px'"
				></changeaset>
			<changeaset :font="'更多正在施工地'"
					:backimg="'/static/img/otherions.png'"
					:backimgposition="'-24px'"
					:color="'#fa4e3e'"
				></changeaset>
		</div>
		
	</div>
</template>
<script>
import Vue from 'vue'
import { Swipe, SwipeItem } from 'mint-ui'
import indexsmolltitle from './indexsmolltitle'
import changeaset from './chnageaset'
Vue.component(Swipe.name, Swipe);
Vue.component(SwipeItem.name, SwipeItem);
	export default {
		name:'indexAppreciate',
//		数据
		data(){
			return {
				imglist: [],
			}
		},
//		需要引入的模板
		components:{
			'indexsmolltitle':indexsmolltitle,
			'changeaset':changeaset
		},
		//函数
		methods: {
			
		},
		mounted(){
			this.$http.get('http://m.yihaojiaju.com/Construction/GetMsProjectInfoBySiteCode').then((response)=> {
				this.imglist = response.body;
			})
		}
		
	}
</script>
<style scoped>
	.indexAppreciate {
		padding-bottom: 10px;
	}
	.indexAppreciate .imglistborder {
		padding: 5%;
		height: 70vw;
	}
	.imglistborder span {
		color:rgb(68, 68, 68);
		font-size: 14px;
		display: inline-block;
		margin: 5px 10px;
	}
	.moreimg {
		background: url(../../static/img/otherions.png) no-repeat;
		width: 16px;
		height: 16px;
		float: right;
		margin: 6px;
		background-size: 100%;
		background-position-y:-36px;
	}
	.allchangebtn {
		display: flex;
		justify-content: space-around;
	}
</style>


