<template>
	<div class="tableborder">
		<div v-for="img in imglist" class="img">
			<a href="javascript:void(0)">
				<img :src="img" alt="" />
			</a>
		</div>
	</div>
</template>
<script>
	export default {
		name:'teblepic',
//		数据
		data(){
			return {
				imglist:["http://tu.yihaojiaju.com/UploadFile/380/DesignerImage/226/201608/16/161529170068.jpg","http://tu.yihaojiaju.com/UploadFile/18/DesignerImage/226/201609/4/041108104482.jpg","http://tu.yihaojiaju.com/UploadFile/1/DesignerImage/226/201606/11/111203454194.jpg","http://tu.yihaojiaju.com/UploadFile/1/DesignerImage/226/201609/8/081039129045.jpg"]
			}
		},
//		需要引入的模板
		components:{
			
		},
		//函数
		methods: {
			changeimg:function(){
				this.imglist = this.imglist.sort(function(){
					return (Math.random() > 0.5)?1:-1
				});
			}
		}
	}
</script>
<style scoped>
.tableborder {
	width: 100vw;
	display: flex;
	/*justify-content: space-around;*/
	
	flex-wrap: wrap;
}
.tableborder .img {
	width: 45.5%;
	margin-left: 3%;
	margin-top: 12px;
}
</style>


