<template>
	<div class="shiyixia">
		<ul
		  v-infinite-scroll="loadMore"
		  infinite-scroll-disabled="loading"
		  infinite-scroll-distance="10">
		  <li v-for="item in list" class="li">{{ item }}</li>
		</ul>
	</div>
</template>
<script>
import Vue from 'vue'
import { InfiniteScroll } from 'mint-ui';
Vue.use(InfiniteScroll);
	export default {
		name:'shiyixia',
//		数据
		data(){
			return {
				list:["1"],
			}
		},
//		需要引入的模板
		components:{
			
		},
		//函数
		methods: {
			loadMore() {
				  this.loading = true;
				  setTimeout(() => {
				    let last = this.list[this.list.length - 1];
				    for (let i = 1; i <= 10; i++) {
				      this.list.push(last + i);
				    }
				    this.loading = false;
				  }, 2500);
			}
		}
	}
</script>
<style scoped>
</style>


