<template>
	<div>
		<div class="love">
			<bigmask v-if="$store.state.maskshow"></bigmask>
			<mylove></mylove>
		</div>
		<div class="head">
			
			<div class="icon">
				<div class="iconback"></div>
				<span>分类</span>
			</div>
			<div class="seekbox">
				<mallseekbox></mallseekbox>
			</div>
			<div class="flexchange" @touchstart="flexchange"></div>
		</div>
		<div class="option">
			<div v-for="(item,index) in activeoption" :class="{activeoption1:index == active1}" @click="changeactive(index)">{{ item }}</div>
		</div>
		<div :class="[classname[zeroorone],'contentlist']">
			<ul ref="see" class=clearfix
				v-infinite-scroll="loadMore"
		 		 infinite-scroll-disabled="loading"
		 	 	infinite-scroll-distance="10">
				<li v-for="item in listconten" class="clearfix">
					<router-link :to="'/gooddetails?&price='+item.price+'&index='+item.index">
						<img :src="item.imglist" alt="" />
						<div>
							<h1>{{ item.tit }}</h1>
							<span class="content">{{ item.content }}</span>
							<span class="price">{{ item.price }}</span>
							<span class="numandgood">{{ item.numandgood }}</span>
						</div>
					</router-link>
				</li>
			</ul>
		</div>
		
	</div>
</template>
<script>
import mallseekbox from "./mallseekbox"
import Vue from 'vue'
import { InfiniteScroll } from 'mint-ui'
import mylove from './mylove'
import bigmask from './bigmask'
Vue.use(InfiniteScroll);
	export default {
		name:'commoditylist',
//		数据
