					price:"¥ 37,500.00",
					numandgood:"已成交:0 (好评率:100%)",
					index:0,
				},{
					imglist:"http://img.yhjj.com/ShopImg/103/134/2014/10/09/184303798.jpg_400x400.jpg",
					tit:"【十三周年庆】",
					content:"欧式宫廷风 转角沙发 精致雕花 实木框架 舒适沙发 ...",
					price:"¥ 27,500.00",
					numandgood:"已成交:10 (好评率:100%)",
					index:1,
				},{
					imglist:"http://img.yhjj.com/ShopImg/103/134/2014/10/09/184451561.jpg_400x400.jpg",
					tit:"【十三周年庆】",
					content:"欧式宫廷风 转角沙发 精致雕花 实木框架 舒适沙发 ...",
					price:"¥ 17,500.00",
					numandgood:"已成交:0 (好评率:100%)",
					index:2,
				},{
					imglist:"http://img125.yhjj.com/ShopImg/103/134/2015/10/03/155348990.jpg_400x400.jpg",
					tit:"【十三周年庆】",
					content:"欧式宫廷风 转角沙发 精致雕花 实木框架 舒适沙发 ...",
					price:"¥ 36,500.00",
					numandgood:"已成交:0 (好评率:100%)",
					index:0,
				},{
					imglist:"http://img125.yhjj.com/ShopImg/103/134/2016/06/30/150549013.jpg_400x400.jpg",
					tit:"【十三周年庆】",
					content:"欧式宫廷风 转角沙发 精致雕花 实木框架 舒适沙发 ...",
					price:"¥ 99,500.00",
					numandgood:"已成交:10 (好评率:100%)",
					index:1,
				},{
					imglist:"http://img.yhjj.com/ShopImg/103/134/2014/10/09/184451561.jpg_400x400.jpg",
					tit:"【十三周年庆】",
					content:"欧式宫廷风 转角沙发 精致雕花 实木框架 舒适沙发 ...",
					price:"¥  56,123.00",
					numandgood:"已成交:0 (好评率:100%)",
					index:2,
				},{
					imglist:"http://img125.yhjj.com/ShopImg/103/134/2015/10/03/155348990.jpg_400x400.jpg",
					tit:"【十三周年庆】",
					content:"欧式宫廷风 转角沙发 精致雕花 实木框架 舒适沙发 ...",
					price:"¥ 8,500.00",
					numandgood:"已成交:0 (好评率:100%)",
					index:0,
				},{
					imglist:"http://img125.yhjj.com/ShopImg/103/134/2016/06/30/150549013.jpg_400x400.jpg",
					tit:"【十三周年庆】",
					content:"欧式宫廷风 转角沙发 精致雕花 实木框架 舒适沙发 ...",
					price:"¥ 27,500.00",
					numandgood:"已成交:10 (好评率:100%)",
				},{
					imglist:"http://img.yhjj.com/ShopImg/103/134/2014/10/09/184451561.jpg_400x400.jpg",
					tit:"【十三周年庆】",
					content:"欧式宫廷风 转角沙发 精致雕花 实木框架 舒适沙发 ...",
					price:"¥ 6667,500.00",
					numandgood:"已成交:0 (好评率:100%)",
					index:1,
				}]
			}
		},
//		需要引入的模板
		components:{
			"mallseekbox":mallseekbox,
			"mylove":mylove,
			"bigmask":bigmask,
		},
		//函数
		methods: {
			flexchange:function(){
				this.zeroorone==0?this.zeroorone=1:this.zeroorone=0;
			},
			loadMore(){
				this.loading = true;
				setTimeout(()=>{
//					let last = this.listconten[this.listconten.length - 1];
					this.listconten.forEach((key,index)=>{
						if(index<7){
							this.listconten.push(key);
						}
					})
					this.loading = false;
				},500)
			},
			changeactive:function(i){
				this.active1 = i;
				this.listconten = this.listconten.reverse();
			}
		},
		mounted(){
//			console.log(this.$refs);
		}
	}
</script>
<style scoped>
	.head {
		height: 50px;
		background: #f5f5f5;
		position: relative;
	}
	.icon {
		height: 50px;
		width: 50px;
		text-align: center;
		color: #f25a52;
		display: inline-block;
		float: left;
	}
	.iconback {
		width: 22px;
		height: 22px;
		background: url(../../static/img/icon-catalog.png);
		background-size: 100%;
		float: left;
		margin: 5px 15px 2px;
		
	}
	.seekbox {
		position: absolute;
		width: 50%;
		height: 30px;
		left: 22%;
		top: 10px;
	}
	.flexchange {
		width: 26px;
		height: 26px;
		background: url(../../static/img/change.png);
		background-size: 100%;
		float: right;
		margin-right: 5%;
		margin-top: 12px;
	}
	.option {
		border-top: 1px solid #dedede;
		border-bottom: 1px solid #dedede;
		display: flex;
	}
	.option div {
		width: 25%;
		padding: 10px;
		text-align: center;
	}
	.option .activeoption1{
		border-bottom: 2px solid #F25546;
	}
	.contentlist {
		font-size: 1.2rem;
	}
	.contentlist h1{
		display: inline-block;
		color: #e4393c;
	}
	.contentlist .price{
		display: block;
		color: #e4393c;
		font-weight: bolder;
		margin: 6px;
		font-size: 16px;
	}
	.contentlist .content {
		color: #333;
	}
	.contentlist .numandgood {
		
		color: #999;
	}
	
	.biglist li{
		border-bottom: 1px solid whitesmoke;
		padding: 1vw 1vh;
	}
	.biglist img {
		margin-right: 3%;
		width: 30%;
		float: left;
	}
	
	/*变化后的class*/
	.smalllist .content{
		overflow : hidden;
		text-overflow: ellipsis;
		display: -webkit-box;
		-webkit-line-clamp: 2;
		-webkit-box-orient: vertical;
	}
	.smalllist li {
		width: 50%;
		margin-bottom: 8px;
		float: left;
	}
	.smalllist li a{		
		display: block;
		padding: 0 8px 0;
	}
	
	.smalllist li:nth-child(2n) a{		
		/*border-left: 1px solid silver;*/
		
	}
</style>


