<!--有大部分按钮样式相同，这里写按钮组件，可被父组件传递文字、logo、颜色（若不传递，则使用文字颜色使用#666666）-->
<template>
	<div class="changebtn" v-on:touchstart="changeorjump">
		<a href="javascript:void(0)":style="{verticalAlign:'middle'}">
			<span :style="{color:color?color:'#666666'}">{{ font }}</span>
			<div :style="{background:'url('+ backimg +') 0% '+backimgposition,backgroundSize:'100%',backgroundPositionX:backimgposition+'px',width:'18px',height:'18px',display:'inline-block',position:'relative',top:'6px'}"></div>
		</a>
	</div>
</template>
<script>
	export default {
		name:'changeaset',
		props:['color','font','backimg','backimgposition'],
//		数据
		data(){
			return {
				
			}
		},
//		需要引入的模板
		components:{
			
		},
		//函数
		methods: {
			changeorjump(){
				this.$emit('changeorjump')
			}
		}
	}
</script>
<style scoped>
	.changebtn {
		width: 38%;
		height: 40px;
		text-align: center;
		font-size: 14px;
		background: #dedede;
		vertical-align:middle;
		line-height:40px;
		font-weight: normal;
		/*padding-top: 8px;*/
		border-radius: 2px;
		backgroundPosition: 0 ;
	}
</style>


