<!-- 标题装修效果，两个可切换的todolist -->
<template>
	<div class="decorationEffect">
		<indexsmolltitle :tit="'装修效果'"></indexsmolltitle>
		<div class='btn'>
			<button v-for="(item,index) in titname" :class="['focut',{'acitve':index == acitve}]" @click="changeactive(index)">
				{{ item }}
			</button>
		</div>
		<div class="piclist" :style="{left:-100*acitve+'%',}">
			<tablepic class="piclist1" ref="piclist1"></tablepic>
			<tablepic class="piclist2" ref="piclist2"></tablepic>
		</div>
		<div class="decorationEffectmore">
			<changeaset :backimg="'/static/img/otherions.png'" :font="'换一批'" :backimgposition="'0px'" @changeorjump="changimg"></changeaset>
			<changeaset :backimg="'/static/img/otherions.png'" :font="'更多效果图'" :color="'#fa4e3e'" :backimgposition="'-24px'"></changeaset>
		</div>
	</div>
</template>
<script>
import indexsmolltitle from './indexsmolltitle'
import tablepic from './tablepic'
import changeaset from './chnageaset'

	export default {
		name:'decorationEffect',
		data(){
			return {
				titname:['风格','户型'],	
				acitve:0,
			}
		},
		methods:{
			changeactive:function(i){
				if(typeof i == "number"){
					this.acitve = i;
					return false;
				}
				this.acitve = (++this.acitve)%2;
			},
			changimg:function(){
				this.$refs.piclist1.changeimg();
				this.$refs.piclist2.changeimg();
			},
			
		},
		components: {
			'indexsmolltitle':indexsmolltitle,
			'tablepic':tablepic,
			'changeaset':changeaset,
		}
	}
</script>
<style scoped>
.decorationEffect {
	position: relative;
	overflow: hidden;
}
.btn {
	position: absolute;
	right: 20px;
	top: 8px;
}
.focut {
	border: none;
	background: rgb(248, 248, 248);
	padding: 12px 0;
	margin: 0 6px;
	outline:none;
	color: #666666;
}
.acitve {
	border-bottom: 3px solid #FA4E3E;
	color: #FA4E3E;
}
.piclist {
	width: 200%;
	position: relative;
	transition: all 0.4s;
	padding: 5px 0;
}
.piclist1 {
	position:absolute;
	right: 0;
}
.decorationEffectmore {
	display: flex;
	justify-content: space-around;
	margin: 6px 0;
}
</style>


