<!-- 购物商城主页 -->
<template>
	<div>
		<div class="header">
			<div class="mask" @click="masknone"v-show="boo">
				<div class="poput" @click="maskshow($event)">
					<div class="close"></div>
					<h1>请选择您的收货地址</h1>
					<div class="addressoption">
						<select name="" @change="pselectchange()" ref='select1'>
							<option v-for="item in pselect" >{{ item["-name"] }}</option>
						</select>
						<select name="" ref='select2'>
							<option v-for="item in cselect">{{ item["-name"] }}</option>
						</select>
					</div>
					<div class="determine" @click="clickdetermine()">确定</div>
				</div>
			</div>
			<div class="address" @click="addressoption">
				<span class="addressicon"></span>
				<span class="addresscontent">{{ $store.state.caddress }}</span>
			</div>
			<div class="seekbox">
				<mallseekbox></mallseekbox>
			</div>
			<div class="headportrait"></div>
		</div>
		<!--轮播图-->
		<indexcarousel-figure 
			:swiperSlides="swiperSlides"></indexcarousel-figure>
		<!-- 加一个导航组件 -->
		<indexflexnav></indexflexnav>	
		<!--图片流-->
		<div class="tithot">
			<h1 class="tit">
				<span></span>
				引爆全场
			</h1>
			<div class="hotimglist">
				<div>
					<router-link to="/commoditylist?id=1">
						<img src="http://img125.yhjj.com/SysImg/141/2016/09/18/103025192.jpg" alt="" />
					</router-link>
				</div>
				<div>
					<div><router-link to="/commoditylist?id=1"><img src="http://img125.yhjj.com/SysImg/141/2016/09/18/092402707.jpg" alt="" /></router-link></div>
					<div><router-link to="/commoditylist?id=1"><img src="http://img125.yhjj.com/SysImg/141/2016/09/18/092654432.jpg" alt="" /></router-link></div>
				</div>
			</div>
		</div>
		<mallgoodscategory :imglist="imglist"></mallgoodscategory>
		<mallgoodscategory :imglist="imglist1"></mallgoodscategory>
		<mallgoodscategory :imglist="imglist2"></mallgoodscategory>
		<mallgoodscategory :imglist="imglist"></mallgoodscategory>
		<mallgoodscategory :imglist="imglist1"></mallgoodscategory>
		<mallgoodscategory :imglist="imglist2"></mallgoodscategory>
	</div>
</template>
<script>
import indexcarouselFigure from './indexcarouselFigure'
import mallseekbox from './mallseekbox'
import mallgoodscategory from './mallgoodscategory'
import indexflexnav from './indexflexnav'
	export default {
		name:'furnishingMall',
//		数据
		data(){
			return {
				swiperSlides: ['http://img125.yhjj.com/SysImg/141/2017/05/22/113659603.jpg','http://img125.yhjj.com/SysImg/141/2017/05/22/113609610.jpg','http://image1.yihaojiaju.com/0/48/SysImg/134/2017/04/17/143830379.jpg','http://img125.yhjj.com/SysImg/141/2017/05/22/113609610.jpg'],
				imglist:["../../static/img/103312904.jpg","../../static/img/183029619.jpg","../../static/img/182458899.jpg","../../static/img/143450848.jpg","../../static/img/143450848.jpg","../../static/img/143450848.jpg"],
				imglist1:["http://img125.yhjj.com/SysImg/170/2015/09/02/180237654.png","http://img125.yhjj.com/SysImg/136/2016/01/25/110903865.jpg","http://img125.yhjj.com/SysImg/136/2016/01/07/135950420.jpg","../../static/img/143450848.jpg","http://img125.yhjj.com/SysImg/141/2016/06/16/102337840.jpg","../../static/img/143450848.jpg"],
				imglist2:["../../static/img/182655017.png","../../static/img/183029619.jpg","../../static/img/182458899.jpg","../../static/img/143450848.jpg","../../static/img/143450848.jpg","../../static/img/143450848.jpg"],
				pselect:[],
				poptionindex:0,
				coptionindex:0,
				boo:true,
				determine:'北京',
				province:"",
				arovince:"",
			}
		},
//		计算属性
		computed:{
				cselect: function (){
					if(this.pselect[this.poptionindex]){
						return this.pselect[this.poptionindex]["city"];
					}else{
						return 1;
					}
					
				},
				
		},
//		需要引入的模板
		components:{
			'indexcarouselFigure':indexcarouselFigure,
			'mallseekbox':mallseekbox,
			'mallgoodscategory':mallgoodscategory,
			'indexflexnav':indexflexnav,
		},
		//函数
		methods: {
				getjson:function(){
					this.$http.get("../../static/json/new.json")
							  .then(function(response) {
							  	this.pselect = response.body.china.province;
					})
				},
				addressoption(){
					this.boo = !this.boo;
				},
				masknone(){
					this.boo = !this.boo;
				},
				maskshow:function(e){
					e.stopPropagation();
				},
				pselectchange:function(){
					this.poptionindex = this.$refs.select1.selectedIndex;
				},
				clickdetermine:function(){
//					console.log(this.$refs.select2.options[this.$refs.select2.selectedIndex].text);
					this.determine = this.$refs.select2.options[this.$refs.select2.selectedIndex].text;
					this.boo = !this.boo;
					this.province = this.$refs.select1.options[this.$refs.select1.selectedIndex].text;
					this.$store.commit('changepaddress',this.province);
					this.$store.commit('changecaddress',this.determine);
					this.coptionindex = this.$refs.select2.selectedIndex;
//					console.log(this.pselect[this.poptionindex]['city'][this.coptionindex]['county'][1]['-name']);
					this.arovince = this.pselect[this.poptionindex]['city'][this.coptionindex]['county'][1]['-name'];
					this.$store.commit('changearea',this.arovince);
					
				}
		},
		mounted(){
			this.getjson();
		}
	}
</script>
<style scoped>
	.header {
		background:#f5f5f5;
		height: 40px;
		position: relative;
		display: flex;
		justify-content: space-between;
	}
	.address {
		float: left;
		margin-left: 3%;
		margin-top: 8px;
	}
	.address >span{
		margin-left: 6px;
	}
	.addresscontent {
		font-size: 13px;
	}
	.addressicon {
		display: inline-block;
		width: 14px;
		height: 18px;
		background: url(../../static/img/locationlogo.png) no-repeat;
		background-size: 100% ;
		background-position-y: 1px;
		position: relative;
		top: 2px;
	}
	.headportrait {
		float: right;
		width: 34px;
		height: 34px;
		border-radius: 50%;
		background: url('http://img125.yhjj.com/vipimg/head/11.jpg');
		background-size: 100% 100%;
		margin-right: 3%;
		margin-top: 3px;
	}	
	.tit {
		height: 40px;
		background: #f2f2f2;
		font-size: 20px;
		line-height: 45px;
		padding-left: 30px;
		position: relative;
	}
	.tit span{
		position: absolute;
		display: inline-block;
		width: 4px;
		height: 20px;
		left: 10px;
		top: 13px;
		border-radius: 2px;
		background: #e14958;
	}
	.hotimglist {
		display: flex;
	}
	.hotimglist >div {
		border: 1px solid #fff;
	}
	.hotimglist >div >div:nth-child(1) {
		border-bottom:1px solid #FFFFFF;
	}
	
	/*搜索条*/
	.seekbox {
		width: 48%;
		position: absolute;
		left: 27%;
		top: 5px;
		height: 26px;
	}
	
	/*弹窗选地址*/
	.mask {
		position: fixed;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		background: rgba(0,0,0,0.6);
		z-index: 2;
	}
	.poput {
		width: 90%;
		height: 165px;
		background: white;
		position: absolute;
		left: 5%;
		top: 15%;
		border-radius: 10px;
	}
	.poput h1 {
		color:rgba(64, 64, 64, 0.86);
		font-size: 20px;
		text-align: center;
		padding-top: 12px;
		
	}
	.poput .addressoption {
		margin-top: 8px;
		/*display: flex;*/
		justify-content: space-around;
	}
	.poput .addressoption select {
		appearance:none;
		width: 40%;
		height: 30px;
		margin: 14px 0 14px 6%;
		border: 1 solid cornflowerblue;
		border-radius: 6px;
		background: url(../../static/img/1.png) no-repeat scroll right center transparent;
		background-size: 10%;
		padding-right: 14px;
		padding-left: 10%;
	}
	.poput .determine{
		width: 70%;
		height: 30px;
		text-align: center;
		line-height: 30px;
		color: white;
		font-size: 18px;
		background: #f25546;
		margin: 10px auto;
		border-radius: 6px;
	}
</style>


