<template>
	<div class="back" @touchstart="clickfunction()">
		<div class="backimg" :style="{background:'url('+ backimg +')',backgroundSize:'100%',}"></div>
		<p>{{ text }}</p>
	</div>
</template>
<script>
	export default {
		name:'functionbtn',
//		数据
		props:["text","backimg"],
		data(){
			return {
				
			}
		},
//		需要引入的模板
		components:{
			
		},
		//函数
		methods: {
			clickfunction(){
				this.$emit('clickfunction')
			}
		}
	}
</script>
<style scoped>
.back {
	background: #858585;
	width: 14vw;
	height: 14vw;
	border-radius: 50%;
	padding-top: 1vw;
	margin: 1vw;
}
.back p {
	color:white;
	text-align: center;
	margin-top: 1vw;
	font-size: 1.2rem;
}
.backimg {
	margin: 0 auto;
	width: 6vw;
	height: 6vw;
}
</style>


