<template>
	<div class="housecompute">
		<h1>
			<div class="smollicon"></div>
			<span>算算你家装修多少钱</span>
		</h1>
		<form class="neworlodhouse">
			<div class="houseoption">
				<mt-radio
				  v-model="value"
				  :options="['新房', '旧房']" 
				  :class="'houseneworlod'">
				</mt-radio>
			</div>
			<div class="area">
				<input type="text" name="" placeholder="请输入面积"/>
				<span class="unit">
					m<sup>2</sup>
				</span>
			</div>
			<div class="housetype">
				<select1 v-bind:optioncontent="optioncontent1"></select1>
				<select1 v-bind:optioncontent="optioncontent2"></select1>
			</div>
			
			<button class="getVaule">一秒拿报价清单</button>
		</form>
		<div class="spread">
			<ul>
				<li v-for="item in spreadcontent">
					{{ item }}
				</li>
			</ul>	
		</div>
	</div>
</template>
<script>
import Vue from 'vue'
import { Radio } from 'mint-ui'
Vue.component(Radio.name, Radio)
import select1 from './select1'

export default {
	name:'housecompute',
	data () {
		return {
			optioncontent1:["现代简约","中国","田园","明国"],
			optioncontent2:["经济环保","白领家居","豪华别墅","贫民窟"],
			spreadcontent:["8万装修的房子，看上去像花了18万！","足不出户装出豪宅，装修费用狂省35%！","足不出户装出豪宅，装修费用狂省35%！"],
			value:"",
		}
	},
	components: {
		'select1':select1
	}
}
</script>
<style>
/*外壳样式*/
.housecompute {
	margin: 20px 0 30px 0;
	box-shadow: 0 0 6px #ffb84e;
}
/*标题*/
.housecompute h1 {
	font-size: 16px;
	position: relative;
	color: #333333;
	font-weight: normal;
	width: 80%;
	left: 10%;
	text-align: center;
	vertical-align: middle;
	height: 48px;
	line-height: 48px;
}
.housecompute h1 .smollicon {
	display: inline-block;
	position: relative;
	width: 10%;
	height: 30px;
	background: url(../assets/img/housecomputephone.png) no-repeat;
	background-size: auto 60%;
	background-position: 15% 55%;
	top: 8px;
}
.housecompute h1 .smollicon::before {
	position: absolute;
	content:" ";
	width: 10px;
	height: 10px;
	background: url(../assets/img/housecomputephoneanimation.png);
	background-size: 100% 100%;
	animation-name: scaleDraw;
	animation-timing-function: ease-in-out;
	animation-iteration-count: infinite;
	animation-duration: 0.7s;
	animation-direction: alternate;
	animation-delay: 0s;
	left: -5px;
	top: 2px;
}
.housecompute h1 span {
	position: relative;
}

@-webkit-keyframes scaleDraw {
        0% {
            -webkit-transform:scale(0.5,0.5);
        }
        100% {
            -webkit-transform:scale(1,1);
        }
}
/*from表单*/
.neworlodhouse {
		border-top: 1px dashed gainsboro;
}

/*单选按钮*/
.houseneworlod{
	display: -webkit-box;
}
.houseneworlod a{
	flex-grow: 0.3;
}
.houseoption {
	text-align: center;
}
.neworlodhouse .area {
	position: relative;
	text-align: center;
}
/*输入面积input标签样式*/
.neworlodhouse .area input{
	width: 88%;
	height: 34px;
	margin: 0 auto;
	border: 1px solid darkgray;
	padding-left:10px;
	border-radius: 2%;
}
.neworlodhouse .area .unit {
	position: absolute;
	right: 7%;
	font-size: 10px;
	top: 6px;
	color: #4b4b4b;
}
/*房子类型框*/
.housetype {
	display: flex;
	justify-content: space-around;
	margin: 8px 0;
}
.housetype select {
	padding-left: 3%;
	height: 34px;
	width: 42%;
	border-radius: 2%;
	color: #333;
	text-align: center;
}

/*获得报价按钮样式*/
.neworlodhouse .getVaule {
	width: 90%;
	height: 32px;
	color: white;
	text-align: 20px;
	font-size: 1.4rem;
	margin: 8px 5%;
	background: #fa4e3e;
	border: 0;
	border-radius: 2%;
}
/*推广*/
.housecompute .spread {
	height: 14px;
	padding: 10px 0px 10px 10%;
	overflow: hidden;
	background: url(../assets/img/indexpread.png) no-repeat;
	background-size: auto 50%;
	background-position: 5% 60%;
	position: relative;
	color:#333333;
}
.housecompute .spread ul {
	position: absolute;
	left: 15%;
	top: 2px;
	animation: 'housecomputespread';
	animation-duration: 8s;
	animation-timing-function: ease;
	animation-delay: 0s;
	animation-iteration-count: infinite;
	animation-direction: alternate;
}
@keyframes housecomputespread {
	0% {
	 	top: 4px;
	}
	5% {
		top: -20px;
	}
	35% {
		top: -20px;
	}
	40% {
		top: -46px;
	}
	80% {
		top: -46px;
	}
	85% {
		top: 4px;
	}
	100% {
		top: 4px;
	}
}
.housecompute .spread ul li{
	margin: 5px 0;	
	font-size: 1.2rem;
}





</style>


