<template>
	<div>
		<HederNavSignin></HederNavSignin>
		<indexcarousel-figure 
			:swiperSlides="swiperSlides"></indexcarousel-figure>
		<flex-nav></flex-nav>
		<house-compute></house-compute>
		<hot-event></hot-event>
		<decoration-effect></decoration-effect>
		<decoration-strategy></decoration-strategy>
		<index-appreciate></index-appreciate>
	</div>
</template>
<script>
import HederNavSignin from './HederNavSignin'
import indexcarouselFigure from './indexcarouselFigure'
import flexNav from './indexflexnav'
import houseCompute from './houSecompute'
import hotEvent from './hotEvent'
import decorationEffect from './decorationEffect'
import decorationStrategy from './decorationStrategy'
import indexAppreciate from './indexAppreciate'

	export default {
		name:"index",
		data(){
			return {
				swiperSlides: ['http://image1.yihaojiaju.com/0/48/SysImg/134/2017/04/10/101326331.jpg','http://image1.yihaojiaju.com/0/48/SysImg/134/2017/04/17/143830379.jpg','http://image1.yihaojiaju.com/0/48/SysImg/134/2017/05/22/102127281.jpg','http://image1.yihaojiaju.com/0/48/SysImg/134/2017/04/17/143830379.jpg','http://image1.yihaojiaju.com/0/48/SysImg/134/2017/04/10/175008933.jpg'],
			}
		},
		components: {
			'indexcarouselFigure':indexcarouselFigure,
			'flexNav':flexNav,
			'houseCompute':houseCompute,
			'hotEvent':hotEvent,
			'decorationEffect':decorationEffect,
			'decorationStrategy':decorationStrategy,
			'indexAppreciate':indexAppreciate,
			'HederNavSignin':HederNavSignin,
		},
		computed:{
			count(){
				return this.$store.state.text;
			},
			ass(){
				return "2";
			}
		},
		mounted(){
			console.log(this.count);
		}
	}
</script>


