<template>
	<div class="alltool" @click="rotate">
		
		<router-link to="/">
			<div class="gohome" :style="{transform: 'translate(0, -'+ top +'%) scale('+scale+') rotate('+ other +'deg)'}"></div>
		</router-link>
		<router-link to="/shopingcart">
			<div class="shopingcart":style="{transform: 'translate('+ top +'%, -'+ left +'%) scale('+scale+') rotate('+ other +'deg)'}"></div>
		</router-link>
		<div class="classify" :style="{transform: 'translate('+ top +'%, '+ left +'%) scale('+scale+') rotate('+ other +'deg)'}"></div>
		<div class="my" :style="{transform: 'translate(0, '+ top +'%) scale('+scale+') rotate('+ other +'deg)'}"></div>
		
			<div class="collection" :style="{transform: 'rotate('+ deg +'deg) scale(0.9)',backgroundColor: color,}">+</div>
		
	</div>
</template>
<script>
	export default {
		name:'',
//		数据
		data(){
			return {
				deg:0,
				iconshow:false,
				top:0,
				left:0,
				color:'#e14958',
				scale:0.3,
				other:0,
			}
		},
//		需要引入的模板
		components:{
			
		},
		//函数
		methods: {
			rotate:function(){
				this.iconshow = !this.iconshow;
				this.deg = this.deg?0:675;
				this.$store.commit("changemaskshow");
				this.top = this.top?0:140;
				this.left = this.left?0:80;
				this.color = this.color=='#e14958'?'#2f2f2f':'#e14958';
				this.scale==0.3?this.scale=1.3:this.scale=0.3;
				this.other = this.other?0:720;
			}
		}
	}
</script>
<style scoped>
	.alltool {
		position: fixed;
		bottom: 15%;
		left: 5%;
		width: 12vw;
		height: 12vw;
		z-index: 3;
	}
	.alltool div {
		position: absolute;
		width: 98%;
		height: 98%;
		background-color: #2f2f2f;
		transition: all 0.3s;
		border-radius: 50%;
	}
	.alltool .collection {
		background: #e14958;
		text-align: center;
		line-height: 11vw;
		color: #fff;
		font-size: 10vw;
		left: -1px;
		top: -1px;
	}
	.my {
		background: url(../../static/img/bar-ico4.png) no-repeat;
		background-size: 100%;
	}
	.classify {
		background: url(../../static/img/bar-ico3.png) no-repeat;
		background-size: 100%;
	}
	.shopingcart {
		background: url(../../static/img/bar-ico2.png) no-repeat;
		background-size: 100%;
	}
	.gohome {
		background: url(../../static/img/bar-ico1.png) no-repeat;
		background-size: 100%;
	}
</style>


