<template>
	<div id="headerNavSignin"  class="clearfix">
		<div class="logo"><a href="javascript:void(0)"></a></div>
		<div class="location clearfix">
			<a href="">全国站</a>
			<i></i>
		</div>
		<a href="javascript:void(0)" class="nav" v-on:click="changenav">导航</a>
		<a href="" class="signin">登录</a>
		<div v-show="navmaskshow"
			class="navmask"
			v-on:click="changenav">
			<div class="squarenavlist">
				<div class="smollnav" v-for="(item,index) in text">
					<router-link to="/furnishingMall">
						<div class="smollnavback" :style="{background: 'url(../../static/img/fixednavicons.png) 0 '+ (index* 12.4)+'%',backgroundSize:'100%'}"></div>
						<p>{{ item }}</p>
					</router-link>
				</div>
			</div>
		</div>
	</div>
</template>
<script>
export default {
	name:'HederNavSignin',
	data(){
		return {
			text:["首页",'家居商城','家居商城','家居商城','家居商城','家居商城','装修攻略','效果图','家居商城'],
			navmaskshow:false,
		}
	},
	methods: {
		changenav:function(){
			this.navmaskshow = !this.navmaskshow;
		}
	}
}
</script>
<style scoped>
#headerNavSignin {
	background: #c51920;
	width: 100%;
	height: 44px;
}
.logo{
	height: 100%;
	width: 28%;
	float: left;
}
.logo >a {
	display: block;
	width: 100%;
	height: 100%;
	background: url(../assets/img/logo.png) no-repeat;
	background-size: 80%;
	background-position: 15px 40%;
}
.location {
	float: left;
	font-size: 1.6rem;
	width: 20%;
	height: 100%;
	line-height: 44px;
}
.location a {
	color: white;
}
.location i {
	display: inline-block;
	background: url(../assets/img/navdror.png) no-repeat;
	width: 20px;
	height: 20px;
	margin-bottom: -9px;
}

#headerNavSignin >a {
	float: right;
	line-height: 70px;
	height: 44px;
	width: 44px;
	vertical-align: middle;
	color: white;
	font-size: 0.8rem;
	text-align: center;
	
}
#headerNavSignin > a:nth-last-child(3){
	background: url(../assets/img/nav.png) no-repeat;
	background-size: 50%;
	background-position: 10px 2px;
	text-align: center;
}
#headerNavSignin > a:nth-last-child(2){
	background: url(../assets/img/signin.png) no-repeat;
	background-size: 50%;
	background-position: 10px 2px;
	text-align: center;
}

.navmask {
	position: fixed;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	background: rgba(0,0,0,0.8); 
	z-index: 2;
}
.squarenavlist {
	background: #ffffff;
	position: absolute;
	width: 90vw;
	height: 90vw;
	left: 0;
	top: 0;
	right: 0;
	bottom: 0;
	margin: auto;
	display: flex;
	justify-content: space-around;
	flex-wrap: wrap;
}
.smollnav {
	border: 1px solid whitesmoke;
	width: 32%;
	flex-grow:1;
	position: relative;
}
.smollnav p{
	position: absolute;
	bottom: 4%;
	width: 100%;
	text-align: center;
	color: #000;
}
.smollnavback {
	position: absolute;
	width: 50%;
	height: 50%;
	left: 25%;
	top: 16%;
	
}
</style>


