<template>
	<div class="shopingcart">
		<div class="header">
			<div class="return" onclick="javascript:history.back(-1)"></div>
			<p>购物车</p>
			<div class="more"></div>
			
		</div>
		<!--当购物车数量shopnum不为0，这个div消失-->
		<div v-if="!$store.state.shopnum" class="ifnone">
			<div class="back"></div>
			<p  class="text">你的购物车空空如也，快去逛逛吧！</p>
			<div class="button">
				<div class="quguangguang" @click="quguangguang">去逛逛</div>
				<div class="signin" @click="signin">登录</div>
			</div>
		</div>
		<div class="seekborder" v-if="$store.state.shopnum">
			<div class="allselected">
				<span class="alloption" :style="{backgroundPosition:allselectedBtn?'0 200%':'0 99%'}" @click="alloption"></span><span>全选</span>
			</div>
			<ul class="seeklist">
				<li v-for="(item,index) in cartDetails">
					<span class="optionGoods" :style="{backgroundPosition:item.boo?'0 200%':'0 99%'}" @click="goodsChangeOption(index)"></span>
					<div class="goodimg">
						<img :src="item.img">
					</div>
					<div class="details">
						<p class="text">欧式宫廷风 皮艺沙发 低调奢华 客厅U型沙发组合 单人位/双人位/三人位/套装 B805-P</p>
						<p class="price">{{ item.price }}</p>
						<div class="num">
							<div class="subtract" @click="subtract(index)">-</div>
							<input type="text" class="input" v-model="item.num">
							<div class="add" @click="add(index)">+</div>
						</div>
					</div>
					<div class="delete">
							<div class="change" @touchstart="changedeleteboo(index)"></div>
							<div class="determine" v-show="item.deleteBoo" @click="deletegoods(index)"></div>
					</div>
				</li>
			</ul>
		</div>
 	<div class="bottomprice">
			<div class="allprice">
				<span class="zongji">总计</span>
				<span class="optionvalue">￥{{ optionvalue }}.00</span>
				<div class="xiaozi">
					<span>商品总额:￥{{ optionvalue }}.00</span>
					<span>返现-￥0</span>
				</div>
			</div>
			<div class="jiesuan" @click="next">结算</div>
		</div> 
	</div>
</template>
<script>
	export default {
		name:'shopingcart',
//		数据
		data(){
			return {
				cartDetails:[],
//				是否全选allselectedBtn
				allselectedBtn:true,
			}
		},
//		计算属性
		computed:{
//			总价optionvalue
			optionvalue:function(){
				return this.cartDetails.reduce(function(a,b){
//					将价格去掉‘,’、¥ ,转换成数字
					var num1 = parseInt(b.price.replace(/,/g,"").substring(2));
//					乘以总数
					num1 = b.boo?num1 * b.num:0;
					return a+num1;
				},0)
			},
		},
//		需要引入的模板
		components:{
			
		},
		//函数
		methods: {
			subtract:function(index){
				this.cartDetails[index].num--;
				if(this.cartDetails[index].num < 1){
					this.cartDetails[index].num = 1;
				}else if(this.cartDetails[index].num > 999){
					this.cartDetails[index].num = 999;
				}
			},
			add:function(index){
				this.cartDetails[index].num++;
				if(this.cartDetails[index].num < 1){
					this.cartDetails[index].num = 1;
				}else if(this.cartDetails[index].num > 999){
					this.cartDetails[index].num = 999;
				}
			},
//			点击某个商品是否选择按钮
			goodsChangeOption:function(index){
				this.cartDetails[index].boo = !this.cartDetails[index].boo;
				var boo = this.cartDetails.every(function(key){
					return key.boo;
				});
				if(boo){
					this.allselectedBtn = true;
				}else {
					this.allselectedBtn = false;
				}
			},
//			全选
			alloption:function(){
				if(this.allselectedBtn){
					this.cartDetails.forEach(function(key){
						key.boo = false;
					})
					this.allselectedBtn =false;
				}else {
					this.cartDetails.forEach(function(key){
						key.boo = true;
					})
					this.allselectedBtn =true;
				}
			},
			changedeleteboo:function(index){
				this.cartDetails[index].deleteBoo = !this.cartDetails[index].deleteBoo;
			},
			deletegoods:function(index){
				this.cartDetails.splice(index,1);
				if(!this.cartDetails.length){
					this.$store.state.shopnum = 0;
				}
			},
			quguangguang:function(){
				this.$router.push('/furnishingMall');
			},
			signin:function(){
				this.$router.push('/signIn');
			},
			next:function(){
				if(this.$store.state.logged){
					alert("敬请期待");
				}else{
					this.$router.push('/signIn');
				}
				
			}
		},
		// 组件被创建
		mounted(){
//			这个好像是让回到顶部按钮消失或者存在
			this.$store.commit('changeshopingnone',false);
//			打开购物车,获取localStorage内的数据赋给cartDetails;让cartDetails来操作;
			this.cartDetails = [];
			for(var i = 0; i<3;i++){
				if(localStorage[i]){
					var obj = JSON.parse(localStorage[i]);
//					加标识
					obj.index = i;
//					加一个选择开关boo,果然本来不存在或者是false
					if(obj.boo == false){
						obj.boo = false;
					}else {
						obj.boo = true;
					}
//					加一个删除按钮开关deleteBoo
					obj.deleteBoo = false;
					this.cartDetails.unshift(obj);
				}
			}
		},
		// 组件被销毁前
		beforeDestroy(){
			this.$store.commit('changeshopingnone',true);
//			销毁localStorage数据，并让cartDetails的数据还给localStorage,顺便将已选择商品数量shopnum改变
			this.$store.state.shopnum = 0;
			for(var i = 0; i < 3;i++){
				localStorage.removeItem(i);
			}
			this.cartDetails.forEach((key)=>{
				this.$store.state.shopnum +=key.num;
				var obj = {};
				obj.boo = key.boo;
				obj.price = key.price;
				obj.img = key.img;
				obj.num = key.num;
				var str = JSON.stringify(obj);
				localStorage.setItem(key.index,str);
			})
		}
	}
</script>
<style scoped>
	.ifnone .back {
		width: 50%;
		height: 150px;
		background: url(../../static/img/shopcartback.png) no-repeat;
		background-size:50% auto;
		margin-left: 25%;
		background-position: 50% 50px;
	}
	.ifnone .text {
		text-align: center;
		color: #c3c3c3;
		font-size: 16px;
	}
	.ifnone .button{
		width: 70%;
		margin-left: 15%;
		display: flex;
		justify-content:space-around;
		font-size: 12px;
		margin-top: 15px;
	}
	.ifnone .button div{
		width: 40%;
		text-align: center;
		border:1px solid #e6e0e0;
		padding: 5px;
		font-size: 16px;
		border-radius: 6px;
	}
	.ifnone .button .quguangguang {
		background-color: #fff;
		color: #666666;
	}
	.ifnone .button .signin {
		background-color: #f15353;
		color: #fff;
	}
	.shopingcart {
		background: #f2f2f2;
		min-height: 100vh;
		position: relative;
	}
	.header {
		background: #e4393c;
		height: 44px;
		line-height: 44px;
		font-size: 1.8rem;
	}
	.header p {
		text-align: center;
		color: white;
	}
	.return {
		left: 3%;
		top: 12px;
		width: 24px;
		height: 24px;
		position: absolute;
		background: url(../../static/img/left-arr.png) no-repeat;
		background-size: auto 100%;
	}
	.header .more {
		width: 24px;
		height: 24px;
		position: absolute;
		background: url(../../static/img/nav-but-icon-2.png) no-repeat;
		background-size: 100% auto;
		right: 3%;
		top: 12px;
	}
	/*ulli样式*/
	.seekborder {
		padding:0px 1.5vw;
		background: white;
	}
	.seekborder li {
		border-bottom: 1px solid #dcdcdc;
		height: 30vw;
		position: relative;
	}
	.optionGoods{
		display: inline-block;
		width: 6vw;
		height: 6vw;
		position: absolute;
		top: 12vw;
		background: url(../../static/img/checkbox-icon.png);
		background-size: 98% 199%;
		background-position-y: 99%;
	}
	.goodimg {
		width: 22vw;
		height: 22vw;
		position: absolute;
		left: 10vw;
		top: 3.5vw;
	}
	.seeklist .details {
		position: absolute;
		width: 50vw;
		left: 37vw;
		top: 2vw;
	}
	.seeklist .details .text {
		white-space:nowrap;
		overflow:hidden;
		text-overflow:ellipsis;
		font-size: 4.2vw;
		color: text-overflow;
	}
	.seeklist .details .price {
		margin: 0.6vw 0;
		color: #f25858;
		font-size: 3.9vw;
	}
	.details .num {
		display:flex ;
		margin-top: 3vw;
		height: 8.3vw;
		width: 65%;
	}
	.details .num div {
		border: 1px solid red;
		width: 34%;	
		text-align: center;
		border: 1px solid #cfcfcf;
		color: #000084;
		line-height: 8.3vw;
		font-size: 4vw;
	}
	.details .num .input {
		width: 33%;	
		border: none;
		padding: 0;
		text-align: center;
		border-bottom: 1px solid #cfcfcf;
		border-top: 1px solid #cfcfcf;
		line-height: 8.3vw;
		color: #000084;
		font-size: 4vw;
	}
	.details .num .subtract {
		border-radius: 5px 0 0 5px;
	}
	.details .num .add {
		border-radius: 0 5px 5px 0;
	}
	/*全选按钮样式*/
	.allselected {
		position: relative;
		height: 10vw;
	}
	.allselected span {
		font-size: 4vw;
		
	}
	.allselected .alloption {
		display: inline-block;
		width: 6vw;
		height: 6vw;
		position: relative;
		top: 2vw;
		background: url(../../static/img/checkbox-icon.png);
		background-size: 98% 199%;
		margin: 0 4vw 0 2vw;
	}
	/*底部结算样式*/
	.bottomprice{
		padding: 3%;
		height: 6.5vh;
		width: 100%;
		background: rgba(0, 0, 0, 0.8);
		position: fixed;
		bottom: 0;
	}
	.bottomprice .allprice{
		width: 60%;
		float: left;
	}
	.allprice {
		font-size: 4.5vw;
	}
	.allprice .zongji{
		color: #fff;
	}
	.allprice .optionvalue{
		color: #f15353;
	}
	.bottomprice .xiaozi{
		font-size: 3.5vw;
		color: #FFFFFF;
	}
	.jiesuan {
		float: left;
		width: 30%;
		margin-top: 0.3vh;
		height: 6vh;
		background: #c2243f;
		text-align: center;
		line-height: 6vh;
		font-size: 2.5vh;
		color: white;
	}
	
	/*删除按钮部分样式*/
	.delete {
		position: absolute;
		width: 7vh;
		left: 89vw;
		top: 2vw;
	}
	.delete div {
		margin-top: 1.3vh;
	}
	.delete .change {
		width: 4vh;
		height: 4vh;
		background: url(../../static/img/car-icon.png);
		background-size: 100%;
	}
	.delete .determine {
		width: 4vh;
		height: 4vh;
		background: url(../../static/img/car-icon.png);
		background-size: 100%;
		background-position-y: 98%;
	}
</style>


