<template>
	<div class="flexnav">
		<a v-for="(item,index) in navtit" class="indextit">
			<div v-bind:style="'backgroundPositionY:'+ Number(index*(14.3)-2) +'%'" class="titback"></div>
			<span>{{ item }}</span>
		</a>		
	</div>
</template>
<script>
	export default {
		name: 'flexnav',
		data() {
			return {
				navtit:['装修预算','效果图','设计师','参观工地','装修攻略','家居帮','家居商城','体验馆']
			}
		}
	}
</script>
	
<style scoped>
.flexnav{
	padding: 10px 0 15px 0;
	display: flex;
	justify-content:space-around;
	flex-wrap: wrap;
}
.indextit {
	width: 21%;
	height:23vw;
	text-align: center;

	
}
.titback {
	width: 100%;
	height: 18vw;
	background: url(../assets/img/navicons.png) no-repeat;
	background-size: 60%;
	background-position-x: 51%;
	color: #666666;
}
</style>


