<template>
	<div class="hotEvent">
		<h1>热门活动</h1>
		<div class="imglistborder" @touchstart="start($event)" 								@touchmove="move($event)"
								@touchend="end()">
			<div class="hotEventiglist clearfix" 
				v-bind:style="{left:imglistleft+'px',transition:boo?'all 0.5s':''}">
				<div class="hotEventimg" v-for="item in imglist">
					<a href="javascript:void(0)">
						<img :src="item">
					</a>
				</div>
			</div>
		</div>
	</div>
</template>
<script>
	export default {
		name:'hotEvent',
//		数据
		data:function(){
			return {
				imglist:["http://img125.yhjj.com/SysImg/141/2017/05/22/111544967.png","http://img125.yhjj.com/SysImg/141/2017/05/22/111705041.png","http://img125.yhjj.com/SysImg/141/2017/05/22/111819786.png","http://img125.yhjj.com/SysImg/141/2017/05/22/111906293.png"],
				imglistleft:0,
				touchstartx:0,
				touchmovex:0,
				touchend:0,
				boo:true,
				initmove:0,
				boo:false,
			}
		},
//		计算属性
		computed: {
			imglistwidth:function(){
				return this.imglist.length * (document.body.offsetWidth/3);
			}
		},
		
//		需要引入的模板
		components:{
			
		},
		//函数
		methods: {
			start:function(e) {
				this.touchstartx = e.changedTouches[0].clientX;
				this.boo = false;
			},
			move:function(e) {
				this.touchmovex = e.changedTouches[0].clientX;
				this.imglistleft = (e.changedTouches[0].clientX- this.touchstartx) + this.initmove;
			},
			end:function(){
				if(this.imglistleft > 0) {
					this.boo = true;
					this.imglistleft = 0;
					
				}else if(this.imglistwidth + this.imglistleft < document.body.offsetWidth){
					this.imglistleft = -(this.imglistwidth - document.body.offsetWidth);
					this.boo = true;
				}
				this.initmove = this.imglistleft;
			}
		}
	}
</script>
<style scoped>
.hotEvent {
	background: rgb(248, 248, 248);
	padding-top: 18px;
}
.hotEvent h1 {
	font-size: 1.6rem;
	height: 30px;
	font-weight: bold;
	padding-left: 12px;
}	
.hotEvent .imglistborder {
	width: 100%;
	overflow: hidden;
	position: relative;
	height: 42vw;
}
.hotEvent .hotEventiglist {
	width: 200%;
	position: absolute;
	
}
.hotEvent .hotEventiglist .hotEventimg {
	width: 33.3vw;
	float: left;
}
</style>


