<template>
	<div>
		<div class="last">
			<a onclick="javascript:history.back(-1)" >&lt</a>
		</div>
		<carrousel :swiperSlides="imglist[$route.query.index]" :class="$route.query.index"></carrousel>
		<div class="content">
			<div class="tit1">
				<span class="red">【13周年品质家装钜献】</span>
				<span>欧式宫廷风 皮艺沙发 低调奢华 客厅U型沙发组合 单人位/双人位/三人位/套装 B805-P</span>
			</div>
			<div class="price">
				<span class="before">回馈价:</span><span class="num">{{ get.price }}</span>
			</div>
			<p class="trafficking">已售123件</p>
			<div class="canorno">
				<span class="gray">送至</span>
				<span class="address">{{ $store.state.paddress }}</span>
				<span class="address">{{ $store.state.caddress }}</span>
				<span class="address">{{ $store.state.area }}</span>
				<span class="can">有货</span>
			</div>
			<div class="optioncolor">
				<span class="color">颜色 :</span>
				<button>栗色</button>
			</div>
			<div class="neednumber">
				<div class="goodsnum">
					数量 :
				</div>
				<div class="addordelect">
					<div class="delete" @touchstart="delect">-</div>
					<input type="text" v-model="value" @input="changetext">
					<div class="add" @touchstart="add">+</div>
				</div>
			</div>
		</div>
		<div class="img">
			<img src="../../static/img/2.png"/>
		</div>
		<div>
	
			<div class="tit">
				<div v-for="(item,index) in titcontent" :class="{active:index == active}"
					@click="changeactive(index)">
					{{ item }}
				</div>
			</div>
			<div class="detailedlist">
				<div class="list1" v-for="(item,index) in fooberimg" v-show="index === active">
					<img :src="item" alt="">
				</div>
			</div>
		</div>
		<div class="bottombutton">
			<div class="love"><div><div></div></div></div>
			<div class="shopingcart"><router-link to="/shopingcart"><em v-if="$store.state.shopnum">{{ $store.state.shopnum }}</em><div></div></router-link></div>
			<div class="addto" @click="addShopingCart">加入购物车</div>
			<div class="nowshop">立即购买</div>
		</div>
	</div>
	
</template>
<script>
import carrousel from './indexcarouselFigure'
	export default {
		name:'',
//		数据
		data(){
			return {
				imglist:[['http://img125.yhjj.com/ShopImg/103/134/2015/10/03/155354809.jpg_400x400.jpg','http://img125.yhjj.com/ShopImg/103/134/2015/10/03/155348990.jpg_400x400.jpg','http://img125.yhjj.com/ShopImg/103/134/2015/10/03/155354809.jpg_400x400.jpg','http://img125.yhjj.com/ShopImg/103/134/2015/10/03/155348990.jpg_400x400.jpg'],['http://img125.yhjj.com/ShopImg/103/134/2016/06/30/150549013.jpg_400x400.jpg','http://img125.yhjj.com/ShopImg/103/134/2016/06/30/150550925.jpg_400x400.jpg','http://img125.yhjj.com/ShopImg/103/134/2016/06/27/145029497.jpg_400x400.jpg'],['http://img125.yhjj.com/ShopImg/103/134/2015/09/26/135640771.jpg_400x400.jpg','http://img125.yhjj.com/ShopImg/103/134/2015/10/03/155354809.jpg_400x400.jpg','http://img125.yhjj.com/ShopImg/103/134/2015/09/26/135656019.jpg_400x400.jpg']],
				get:{},
				value:1,
				titcontent:["图文详情","用户评价","售后保证"],
				active:0,
				fooberimg:["../../static/img/3.png","../../static/img/4.png","../../static/img/3.png"],
			}
		},
//		需要引入的模板
		components:{
			'carrousel':carrousel,
		},
		//函数
		methods: {
			delect:function(){
				this.value--;
				if(this.value >999){
					this.value = 999;
				}else if(this.value < 1){
					this.value = 1;
				}
			},
			add:function(){
				this.value++;
				if(this.value >999){
					this.value = 999;
				}else if(this.value < 1){
					this.value = 1;
				}
			},
			changetext:function(){
				if(this.value >999){
					this.value = 999;
				}else if(this.value < 1){
					this.value = 1;
				}
			},
			changeactive:function(index){
				this.active = index;
			},
			addShopingCart:function(){
				if(localStorage.getItem(this.get.index)){
					var str = localStorage.getItem(this.get.index);
					var obj = JSON.parse(str);
					obj.num+=this.value; 
					str = JSON.stringify(obj);
					localStorage.setItem(this.get.index,str);
				}else {
					var obj = {};
					obj.price = this.get.price;
					obj.img = this.imglist[this.get.index][this.get.index];
					obj.num = this.value;
					var str = JSON.stringify(obj);
					localStorage.setItem(this.get.index,str);
				}

//				obj.img = this.imglist[0];
				this.$store.commit('addshopnum',this.value);
//				obj.num = this.
//				console.log(this.$route.query.index)
			}
		},
		created:function(){
			this.get = this.$route.query;
		}
	}
</script>
<style scoped>
	.last{
		position: absolute;
		left: 4%;
		top: 1%;
		width: 40px;
		height: 40px;
		line-height: 40px;
		background:rgba(0, 0, 0, 0.4);
		font-size: 30px;
		color: rgb(222, 222, 222);
		z-index: 2;
		border-radius: 50%;
	}
	.last a {
		position: absolute;
		top: -2px;
		left: 7px;
		transform: scaleY(1.2);
	}
	.content {
		margin: 10px 10px 0 10px;
	}
	.tit1 .red {
		font-size: 1.4rem;
		color:#cc0033;
	}
	.price {
		margin: 10px 2%;
	}
	.price .before{
		display: inline-block;
		color: #f25546;
		margin: 0 5%;
		font-size: 1.2rem;
	}
	.price .num {
		color: #f25546;
		font-size: 2.4rem;
		font-weight: bold;
	}
	.trafficking {
		color: #999999;
		margin-left: 5%;
	}
	.canorno {
		margin-top: 4px;
		padding-left: 4%;
	}
	.canorno .gray{
		color: #999999;
		font-size: 1.4rem;
		display: inline-block;
		margin-right: 2%;
	}
	.canorno .can {
		display: inline-block;
		margin-left: 2%;
		color: #fa4e3e;
		font-size: 1.4rem;
	}
	.optioncolor {
		padding: 2vw;
		border-top: 1px dashed #cfcfcf;
		height: 40px;
		line-height: 40px;
	}
	.optioncolor .color{
		color: #b7b7b7;
		font-size: 1.4rem;
		
	}
	.optioncolor button {
		margin-left: 2%;
		background: white;
		border: 1px solid #f15352;
		color: #f15352;
		border-radius: 3px;
	}
	.neednumber {
		border-top: 1px dashed #cfcfcf;
		height: 40px;
		padding: 3vw;
		border-top: 1px dashed #cfcfcf;
		height: 40px;
		line-height: 40px;
	}
	.goodsnum {
		color: #b7b7b7;
		font-size: 1.4rem;
		float: left;
		margin-right: 10px;
	}
	.addordelect{
		display: flex;
	}
	.addordelect div{
		width: 42px;
		height: 36px;
		line-height: 36px;
		font-size: 12px;
		text-align: center;
		border: 1px solid #cfcfcf;
		color: #cfcfcf;
		
	}
	.addordelect .delete {
		border-radius: 5px 0 0 5px;
	}
	.addordelect .add {
		border-radius: 0 5px 5px 0;
	}
	.neednumber input{
		width: 44px;
		height: 36px;
		border: none;
		text-align: center;
		border-top: 1px solid #cfcfcf;
		border-bottom: 1px solid #cfcfcf;
		text-align: 36px;
	}
	.tit {
		display: flex;
	}
	.tit div{
		height: 12px;
		padding: 12px 2px;
		border-top: 1px solid gainsboro;
		border-bottom: 1px solid gainsboro;
		width: 34%;
		text-align: center;
		line-height: 12px;
		color: #f25546;
		font-size: 12px;
		border-left: 1px solid gainsboro;
	}
	.tit .active {
		color: white;
		background: #f25546;
	}
	.bottombutton {
		position: fixed;
		bottom: 0;
		height: 40px;
		width: 100%;
		background: #f7f7f7;
	}
	.bottombutton div {
		float: left;
		color: white;
		font-size: 1.4rem;
		text-align: center;
		line-height: 40px;
	}
	.bottombutton .love {
		width: 20%;
	}
	.bottombutton .love >div{
		background: #f7f7f7;
		height: 40px;
		border-right: 1px solid gainsboro;
		width: 95%;
		position: relative;
	}
	.bottombutton .love div div {
		width: 34px;
		height: 34px;
		position: absolute;
		left: 0;
		top: 0;
		right: 0;
		bottom: 0;
		margin: auto;
		background: url(../../static/img/icon-collection.png) no-repeat;
		background-size: auto 100%;
		background-position: auto 10px;
	}
	.bottombutton .shopingcart {
		width: 20%;
		height: 40px;
		background: #f7f7f7;
		position: relative;
	}
	.bottombutton .shopingcart em {
		position: absolute;
		display: inline-block;
		border-radius: 50%;
		font-size: 10px;
		width: 20px;
		height: 20px;
		text-align: center;
		line-height: 18px;
		color: white;
		font-weight: bold;
		background: red;
		right: 6%;
		
	}
	.bottombutton .shopingcart div{
		width: 34px;
		height: 34px;
		position: absolute;
		left: 0;
		top: 0;
		right: 0;
		bottom: 0;
		margin: auto;
		background: url(../../static/img/icon-cart.png) no-repeat;
		background-size: auto 100%;
		background-position: auto 10px;
	}
	.bottombutton .addto{
		width: 30%;
		height: 40px;
		background: #f4800e;
	}
	.bottombutton .nowshop {
		width: 30%;
		height: 40px;
		background: #f25546;
	}
	
	/*地址*/
	.address {
		font-size: 14px;
		color: #484848;
	}
</style>


